.list {
    display: flex;
    flex-direction: column;
    align-items: center;
    width:100%;
    &_title {
        font-size: 26px;
        color: #ffff;
    }
    &_list {
        list-style: square;
        color: tomato;
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        row-gap: 20px;
        &_item{
            background: #FFF;
            border-radius: 8px;
            padding: 15px 10px;
            transition-duration: 0.3s;
            &:hover{
                transition-duration: 0.3s;
                background: #c14f3b;
                &>a{
                    transition-duration: 0.3s;
                    color: #FFF;
                }
            }
           &_link {
            transition-duration: 0.3s;
           font-size: 18px;
           text-decoration: none;
           color: tomato; 
           } 
        }
    }
}